@import './variables';
@import './libs/normalize';
@import './components/all';

body {
   background-color: $background;
}

.wrapper {
   width: calc(100vw - 100px);
   height: 100%;
   background-color: #fff;
   margin: 50px auto;
   border-radius: 10px;
   max-width: 1400px;
}

.container {
   width: $container-width;
   margin: 0 auto;

   &--cart {
      max-width: 820px;
      margin: 90px auto;

      .content__title {
         margin: 0;
      }
   }
}

.content {
   padding: 40px 0;

   &__title {
      margin: 35px 0;
   }

   &__items {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
   }

   &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
   }
}

